<template>
  <div class="container">
    <h1>Latest Posts</h1>

    <div class="create-post">
      <label for="create-post">Whats on your mind?</label>
      <input type="text" id="create-posts" v-model="text" placeholder="Add Post">
      <button v-on:click="createPost">Post</button>



    </div>

    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div class="post-container">
      <div class="posts" v-for="(post, index) in posts" 
      v-bind:item="post" 
      v-bind:index="index" 
      v-bind:key="post._id">

      {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}



      <p class="text">{{post.text}}</p>

        <button @click="deletePost(post._id)" class="del">x</button>


      
      
      </div>
    </div>
 
  </div>
</template>

<script>

import PostService from "../postService"
export default {
  name: 'posts',

  data () {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },

  async created () {
    try {
      this.posts = await PostService.getPosts();
    }
    catch (err){ 
      this.error = err.message;

    }
  },

  methods: {
    createPost: async function (text) {
      await PostService.insertPost(this.text)
      this.posts = await PostService.getPosts();

    },
    
    deletePost: async function (id) {
      await PostService.deletePost(id)
      this.posts = await PostService.getPosts();

    }

    
  }
}
</script>


